export function addTodo(state, action) {
    state[action.id] = { label: action.label, completed: false };
}
export function remove(state, action) {
    delete state[action.id];
}
export function complete(state, action) {
    state[action.id].completed = !state[action.id].completed;
}
export function clear(state) {
    Object.keys(state).forEach(function (key) {
        if (state[key].completed) {
            delete state[key];
        }
    });
}
export function setFilter(state, action) {
    return action.filter;
}
