import { addTodo, remove, complete, clear, setFilter } from './pureFunctions';
import { combineReducers } from 'redux';
import { createReducer } from 'redux-starter-kit';
var todoReducer = createReducer({}, {
    addTodo: addTodo,
    remove: remove,
    clear: clear,
    complete: complete
});
var filterReducer = createReducer('all', {
    setFilter: setFilter
});
export var reducer = combineReducers({
    todos: todoReducer,
    filter: filterReducer
});
